<script setup>

import bottom from '@/components/BottomView.vue'
import { useRouter, useRoute } from 'vue-router';
import useClipboard from 'vue-clipboard3'
import i18n from '@/i18n';
import { reactive, ref, watch, onMounted, onUnmounted, getCurrentInstance } from 'vue';
import { Toast } from 'vant';
import { getBanner, getNotice, getTotalPower, getUser } from '@/utils/api'
import axios from 'axios';
import hyc from '@/json/hyc.json'
import sad from '@/json/sad.json'
import { ethers, utils } from 'ethers';

const router = useRouter()
const route = useRoute()
const timer = ref(0)
const { proxy } = getCurrentInstance()
const { toClipboard } = useClipboard()
const state = reactive({
    totalPower: 0,
    userInfo: {},
    bannerList: [],
    noticeList: [],

    hycContractAddress: process.env.VUE_APP_HYC_CONTRACT_ADDRESS,
    hycContract: null,
    sadContractAddress: process.env.VUE_APP_SAD_CONTRACT_ADDRESS,
    sadContract: null,
    hycBalance: 0,
    sadBalance: 0,
})

watch(() => route.account, () => {
    getUserInfo()
    getBalance()
})

onMounted(async () => {
    state.hycContract = new ethers.Contract(state.hycContractAddress, hyc, proxy.$provider.getSigner())
    state.sadContract = new ethers.Contract(state.sadContractAddress, sad, proxy.$provider.getSigner())
    axios.all([
        await getBanner(),
        await getNotice(),
    ]).then(axios.spread(function (banner, notice) {
        state.bannerList = banner.data
        state.noticeList = notice.data
    }))
    getTotalPowerInfo()
    getUserInfo()
    getBalance()
    setIntervalRefreshData()
})

const setIntervalRefreshData = (() => {
    timer.value = window.setInterval(async () => {
        getTotalPowerInfo()
        getUserInfo()
        getBalance()
    }, 3000)
})
const clearIntervalRefreshData = (() => {
    window.clearInterval(timer.value)
})

onUnmounted(() => {
    clearIntervalRefreshData()
})

const getTotalPowerInfo = (async () => {
    const { data } = await getTotalPower()
    state.totalPower = data.total
})
const getUserInfo = (async () => {
    if (route.account && route.registered) {
        const { data } = await getUser({
            address: route.account
        })
        state.userInfo = data
    }
})
const getBalance = (async () => {
    if (route.account) {
        const hycBalanceOf = await state.hycContract.balanceOf(route.account)
        state.hycBalance = parseFloat(utils.formatEther(hycBalanceOf))
        const sadBalanceOf = await state.sadContract.balanceOf(route.account)
        state.sadBalance = parseFloat(utils.formatEther(sadBalanceOf))
    }
})

const checkUserState = (() => {
    if (!route.account) {
        Toast.fail(i18n.global.t('toast.pleaseConnectWallet'))
        return false
    }
    if (!route.registered) {
        Toast.fail(i18n.global.t('toast.pleaseRegister'))
        route.isShowRegister = true
        return false
    }
    return true
})

const clickNotice = (() => {
    router.push('notice')
})
const clickHYCFluidity = (() => {
    if (checkUserState()) {
        router.push('hycFluidity')
    }
})
const clickSADFluidity = (() => {
    if (checkUserState()) {
        router.push('sadFluidity')
    }
})
const clickSADStaking = (() => {
    if (checkUserState()) {
        router.push('sadStaking')
    }
})
const clickApplyNode = (() => {
    if (checkUserState()) {
        router.push('applyNode')
    }
})
const clickInvitationList = (() => {
    router.push('invitationList')
})

const clickCopyActivationAddress = (async () => {
    try {
        await toClipboard(route.account)
        Toast.success(i18n.global.t('toast.copySuccess'))
    } catch (error) {
        Toast.fail(i18n.global.t('toast.copyFail'))
    }
})
const clickCopyParent = (async () => {
    try {
        await toClipboard(route.parent)
        Toast.success(i18n.global.t('toast.copySuccess'))
    } catch (error) {
        Toast.fail(i18n.global.t('toast.copyFail'))
    }
})

</script>

